// rotates variants (param: x deg)

$rotates: (0 : 0,
45 : 45,
90 : 90,
180 : 180,
270 : 270,
360 : 360,
);


@each $class,
$value in $rotates {

  .rotate-#{$class} {
    @include rotate(-$value) ;
  }

  .rotate-x-#{$class} {
    @include rotateY($value);
  }

  .rotate-y-#{$class} {
    @include rotateY($value);
  }

}